.contenedor_loader{
    position: relative;
    transition: all 1s;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 0;
    margin-top: 24px;
}

.loader,
.loader:before,
.loader:after {
    border-radius: 50%;
    width: 2.5em;
    height: 2.5em;
    animation: loader 1.5s infinite ease-in-out;
}

.loader{
    color: #4685fa;
    font-size: 12px;
    position: relative;
    animation-delay: -0.16s;
}

.loader:before,
.loader:after{
    content: '';
    position: absolute;
    top: 0;
}

.loader:before{
    left: -3.5em;
    animation-delay: -0.32s;
}

.loader:after{
    left: 3.5em;
}

@keyframes loader{
    0%,
    80%,
    100% {
        box-shadow:0 2.5em 0 -1.3em;
    }
    40% {
        box-shadow:0 2.5em 0 0;
    }
}